  margin-right: 5%; }

.hentry .xe-post-title .xe-title {
  font-size: 24px;
  font-weight: 600; }

.xe-post-metas,
.xe-post-excerpt > p,
.hentry .xe-post-title .xe-title {
  margin-bottom: 15px; }

.xe-post-metas > ul {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center; }

.xe-post-metas .meta {
  position: relative;
  font-size: calc( var(--xe_base_font_size) - 2px);
  margin-right: 15px;
  padding-right: 15px; }

.xe-post-metas .meta::after {
  content: "";
  display: inline-flex;
  position: relative;
  right: auto;
  left: 15px;
  height: 10px;
  width: 1px;
  background-color: #d6d6d6; }

.xe-post-metas.slanted-seperator .meta:after {
  transform: rotate(20deg); }

.xe-post-metas.no-seperator .meta {
  margin-right: 15px;
  padding-right: 0; }
  .xe-post-metas.no-seperator .meta::after {
    content: "";
    display: none; }

.xe-post-metas > ul > .meta:last-child::after {
  content: "";
  display: none; }

.xe-post-metas .meta.author .xe-avatar {
  display: inline-flex;
  margin-right: 5px;
  max-width: 30px; }

.xe-post-metas .meta.date::before,
.xe-post-metas .meta.comments::before,
.xe-post-metas .meta.categories::before {
  content: "\e6c6";
  font-family: var(--xe_iconfont);
  margin-right: 2px; }

.xe-post-metas .meta.categories::before {
  content: "\e617"; }

.xe-post-metas .meta.comments::before {
  content: "\e645"; }

.xe-post-metas .meta:last-child {
  margin-right: 0; }

.xe-post-metas .meta.categories > a {
  margin-right: 2px; }

.xe-post-metas .meta.categories > a::after {
  content: ","; }

.xe-post-metas .meta.categories > a:last-child {
  margin-right: 0; }
  .xe-post-metas .meta.categories > a:last-child::after {
    content: ""; }

.search.xe-no-sidebar #xe-page #content > .xe-container {
  max-width: var(--xe_no_sidebar_narrow_container_width); }

.error404 #xe-page #primary {
  margin-top: 60px;
  margin-bottom: 60px; }

.error404 .content-is-404 {
  text-align: center; }

.error404 .content-is-404 .xe-title {
  font-family: var(--xe_system_font_family);
  font-size: 186px;
  font-weight: bold;
  line-height: 1em;
  margin-bottom: 0; }

.error404 .content-is-404 .xe-subtitle {
  position: relative;
  font-weight: 500;
  margin-bottom: 15px;
  padding-bottom: 15px; }
  .error404 .content-is-404 .xe-subtitle::after {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 3px;
    width: 100px;
    margin: 0 auto;
    background-color: var(--xe_border_color); }

.error404 .content-is-404 .xe-excerpt {
  max-width: 400px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto; }

.error404 .xe-gohome .xe-button .icon {
  margin-left: 10px; }

@media (max-width: 575px) {
  .error404 .content-is-404 .xe-title {
    font-size: 92px; } }

.xe-product-rating {
  margin-bottom: 5px; }

.xe-product-rating .rating {
  font-size: 13px;
  color: #d5d5d5; }

.xe-product-rating.one .rating:nth-child(1) {
  color: #ffb41d; }

.xe-product-rating.two .rating:nth-child(1),
.xe-product-rating.two .rating:nth-child(2) {
  color: #ffb41d; }

.xe-product-rating.three .rating:nth-child(1),
.xe-product-rating.three .rating:nth-child(2),
.xe-product-rating.three .rating:nth-child(3) {
  color: #ffb41d; }

.xe-product-rating.four .rating:nth-child(1),
.xe-product-rating.four .rating:nth-child(2),
.xe-product-rating.four .rating:nth-child(3),
.xe-product-rating.four .rating:nth-child(4) {
  color: #ffb41d; }

.xe-product-rating.five .rating:nth-child(1),
.xe-product-rating.five .rating:nth-child(2),
.xe-product-rating.five .rating:nth-child(3),
.xe-product-rating.five .rating:nth-child(4),
.xe-product-rating.five .rating:nth-child(5) {
  color: #ffb41d; }

.xe-product-swatch {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center; }

.xe-product-swatch .swatch {
  display: inline-flex;
  height: 15px;
  width: 15px;
  margin-right: 10px;
  border-radius: 100%; }
  .xe-product-swatch .swatch:last-child {
    margin-right: 0; }

.xe-product-swatch .swatch.red {
  background-color: #fb8585; }

.xe-product-swatch .swatch.blue {
  background-color: #8c8cff; }

.xe-product-swatch .swatch.green {
  background-color: #6dff6d; }

.xe-product-swatch .swatch.purple {
  background-color: #ff6dff; }

.xe-product-swatch.visible-on-hover {
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s ease; }

.xe-woo-product:hover .xe-product-swatch.visible-on-hover {
  visibility: visible;
  opacity: 1;
  animation: xeButtonFadeInUp 1s 0s; }

